/* LESS Document _colors.less */
/* Smokefree Family Colors */
.red {
  color: #94261B;
}
/* LESS Document _text.less */
body {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 16px;
  font-weight: 300;
}
h1 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 48px;
  font-weight: 700;
  line-height: 52px;
  margin-top: 30px;
  margin-bottom: 20px;
}
h2 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 36px;
  font-weight: 600;
  line-height: 40px;
  margin-top: 30px;
  margin-bottom: 10px;
}
h3 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 24px;
  font-weight: 600;
  line-height: 28px;
  margin-top: 20px;
  margin-bottom: 0px;
}
h4 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 20px;
  font-weight: 700;
  line-height: 26px;
  letter-spacing: 0.05em;
  margin-top: 20px;
  margin-bottom: 0px;
}
h1,
h2,
h3,
h4 {
  color: #2a2a2a;
}
p {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 16px;
  line-height: 26px;
  font-weight: 300;
}
p.intro {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 24px;
  line-height: 36px;
  font-weight: 300;
}
p.larger,
label.larger {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 20px;
  line-height: 32px;
  font-weight: 300;
}
ul > li,
ol > li {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 16px;
  line-height: 26px;
  font-weight: 300;
  margin-bottom: 5px;
}
ul > li:last-of-type,
ol > li:last-of-type {
  margin-bottom: 0;
}
.first {
  margin-top: 0 !important;
}
.last {
  margin-bottom: 0 !important;
}
blockquote {
  font-family: "Cormorant Garamond", "Garamond", "Times New Roman", Times, "serif";
  font-size: 48px;
  line-height: 52px;
  font-style: italic;
  font-weight: 300;
  color: #000000;
  padding: 0;
  margin: 0;
  border-left: none;
  position: relative;
}
blockquote::before {
  content: "\201C";
  position: absolute;
  left: -12px;
}
blockquote::after {
  content: '';
}
blockquote.small-quote {
  font-size: 32px;
  line-height: 38px;
}
cite {
  font-size: 18px;
  line-height: 20px;
  font-style: normal;
  padding: 20px 20px 10px;
  display: block;
  position: relative;
}
cite::before {
  content: "\2013";
  position: absolute;
  left: 5px;
}
cite::after {
  content: '';
}
.quote-block {
  background-color: #FFFFFF;
  padding: 20px 10px;
  margin: 40px 0;
}
.quote-block .btn {
  float: right;
}
.quote-block:before,
.quote-block:after {
  display: table;
  content: " ";
}
.quote-block:after {
  clear: both;
}
/* Circle, Text in a Circle */
.circle {
  width: 80px;
  height: 80px;
  border-radius: 50%;
  font-size: 18px;
  color: #000000;
  line-height: 80px;
  text-align: center;
  background-color: #f2f2f2;
}
/* Callouts */
.callout {
  background-color: #f2f2f2;
  border: 1px solid #dddddd;
  border-radius: 3px;
  padding: 20px;
  margin: 40px 0;
}
.callout p {
  font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 24px;
  line-height: 36px;
  font-weight: 300;
}
.callout .btn {
  float: right;
}
.callout:before,
.callout:after {
  display: table;
  content: " ";
}
.callout:after {
  clear: both;
}
@media (max-width: 1200px) {
  .callout p {
    font-size: 22px;
    line-height: 32px;
  }
}
@media (max-width: 992px) {
  .callout p {
    font-size: 20px;
    line-height: 28px;
  }
}
@media (max-width: 768px) {
  .callout p {
    font-size: 18px;
    line-height: 24px;
  }
}
/* Sidebar Quit Notes */
.title_block {
  background-color: #737373;
}
.title_block h2 {
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 20px;
  font-weight: 700;
  line-height: 24px;
  letter-spacing: 0.13em;
  text-align: center;
  color: #FFFFFF;
  margin: 0;
  padding: 20px;
  text-transform: uppercase;
  width: 100%;
}
