  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 70px;
  font-weight: 700;
  line-height: 62px;
  letter-spacing: 0;
  text-transform: none;
  text-align: left;
  margin: 0;
  padding-bottom: 20px;
  padding-right: 20px;
}
.tools_tips_wrapper {
  position: relative;
  width: 100%;
}
.tools_tips_wrapper ul.tools_tips_list {
  list-style: none;
  margin: 0;
  padding: 0;
}
.tools_tips_wrapper ul.tools_tips_list li {
  float: left;
  width: 150px;
  margin-top: 50px;
  margin-right: 10px;
}
.tools_tips_wrapper ul.tools_tips_list li a {
  width: 150px;
  height: 150px;
  position: relative;
  top: 0px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  display: block;
  padding: 70px 15px 15px 15px;
  font-family: "Montserrat", "Helvetica Neue", Helvetica, Arial, "sans-serif";
  font-size: 14px;
  font-weight: 600;
  line-height: 18px;
  background-color: #087599;
  color: #FFFFFF;
  text-decoration: none;
}
.tools_tips_wrapper ul.tools_tips_list li a::before {
  content: '';
  display: block;
  position: absolute;
  top: -70px;
  left: 20px;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  width: 110px;
  height: 110px;
  border-radius: 50%;
  border: 5px solid #ffffff;
  background-image: url('/images/toolstips-icons-2x.png');
  background-repeat: no-repeat;
  background-size: 110px 550px;
  background-color: #087599;
}
.tools_tips_wrapper ul.tools_tips_list li:nth-of-type(1) a::before {
  background-position: -5px -5px;
}
.tools_tips_wrapper ul.tools_tips_list li:nth-of-type(2) a::before {
  background-position: -5px -115px;
}
.tools_tips_wrapper ul.tools_tips_list li:nth-of-type(3) a::before {
  background-position: -5px -225px;
}
.tools_tips_wrapper ul.tools_tips_list li:nth-of-type(4) a::before {
  background-position: -5px -335px;
}
.tools_tips_wrapper ul.tools_tips_list li:nth-of-type(5) a::before {
  background-position: -5px -445px;
}
.tools_tips_wrapper ul.tools_tips_list li:last-of-type {
  margin-right: 0px;
}
.tools_tips_wrapper ul.tools_tips_list li a:focus,
.tools_tips_wrapper ul.tools_tips_list li a:hover {
  top: -15px;
  background-color: #064d80;
  color: #FFFFFF;
  text-decoration: none;
  -webkit-box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.15);
          box-shadow: 0px 0px 5px 3px rgba(0, 0, 0, 0.15);
}
.tools_tips_wrapper ul.tools_tips_list li a:focus::before,
.tools_tips_wrapper ul.tools_tips_list li a:hover::before {
  top: -55px;
}
.tools_tips_wrapper:before,
.tools_tips_wrapper:after {
  display: table;
  content: " ";
}
.tools_tips_wrapper:after {
  clear: both;
}
.tools_tips_jumpers {
  display: none;
}
