#flip-card {
  position: relative;
  height: 250px;
  background: #fff;
  -webkit-perspective: 800px;
  -o-perspective: 800px;
  perspective: 800px;
}
#flip-card .card {
  margin-top: 0px;
  padding: 0px;
  width: 100%;
  height: 100%;
  background-color: transparent;
  border: 0;
  position: absolute;
  -webkit-transition: -webkit-transform 0.3s;
  -o-transition: -o-transform 0.3s;
  transition: -webkit-transform 0.3s;
  -o-transition: transform 0.3s;
  transition: transform 0.3s;
  transition: transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
}
#flip-card .card .ticker {
  text-align: right;
  font-size: 13px;
  font-weight: 400;
  background-color: #FFFFFF;
  border: 1px solid #dddddd;
  padding: 4px 6px;
  width: auto;
  display: inline-block;
  float: right;
}
#flip-card .card .front,
#flip-card .card .back {
  display: block;
  height: 100%;
  width: 100%;
  padding: 20px;
  position: absolute;
  background-color: #f2f2f2;
  border: 1px solid #dddddd;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}
#flip-card .card .front .flip-card-wrapper,
#flip-card .card .back .flip-card-wrapper {
  height: 150px;
}
#flip-card .card .front .flip-card-wrapper .flip-card-title,
#flip-card .card .back .flip-card-wrapper .flip-card-title {
  margin-top: 20px;
}
#flip-card .card .back {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
#flip-card .flipped {
  -webkit-transition: -webkit-transform 0s;
  -o-transition: -o-transform 0s;
  transition: -webkit-transform 0s;
  -o-transition: transform 0s;
  transition: transform 0s;
  transition: transform 0s, -webkit-transform 0s;
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}


